.color-palet{
  background: #FFF;
}

.color-palet .palet-header{
    background-color: #d5414f;
    height: 10rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    padding: 0 4rem;
}
.color-palet .palet-header img{
    width: 2rem;
    height: 3.6rem;
}
.color-palet .palet-header span{
    font-size: 3.4rem;
    color: #fff;
    font-weight: 600;
    padding-left: 8rem;
}
.color-palet .color-palet-content{
    padding: 2.5rem 4rem 0 4rem;
}
.color-palet .color-palet-content .color-palet-items{
    padding: 2.5rem 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    position: relative;
}
.color-palet .color-palet-content .color-palet-items .items-color{
    height: 3rem;
    width: 3rem;
    border-radius: 50%;
}
.color-palet .color-palet-content .color-palet-items .items-text{
    padding-left: 7rem;
    font-size: 3rem;
}
.color-palet .color-palet-content .color-palet-items .items-active img{
    width: 4.5rem;
    height: 3rem;
    margin-bottom: 3rem;
}
.color-palet .color-palet-content .color-palet-items .items-active{
    position: absolute;
    right: 0;
}


