#alert-modal {
    height: 32rem;
    top: 40rem;
}

#alert-modal .cancel img {
    text-align: left;
    background-color: #fff;
    padding: 0 0.2rem !important;
    width: 2.5rem;
    height: 2.2rem;
}

#alert-modal .field {
    padding-top: 7rem;
}

#alert-modal .form-control {
    height: 10rem;
    border: none;
    border-bottom: 0.3rem solid #e8e7e6;
    -webkit-box-shadow: none;
            box-shadow: none;
}

#alert-modal .form-control::-webkit-input-placeholder {
    font-size: 3.2rem;
    color: #cccccc;
}

#alert-modal .form-control::-ms-input-placeholder {
    font-size: 3.2rem;
    color: #cccccc;
}

#alert-modal .form-control::placeholder {
    font-size: 3.2rem;
    color: #cccccc;
}

#alert-modal .form-control:focus {
    border-bottom: 0.3rem solid #00b1c3;
    -webkit-box-shadow: none;
            box-shadow: none;
