}

#alert-modal .title {
    font-size: 3rem;
    color: #1a1406;
    text-align: center;
    /* padding-top: 5.5rem; */
    font-weight: 600;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}

#alert-modal .alert-modal_content {
    position: absolute;
    width: 100%;
    left: 0;
    /* top: 12.5rem; */
}


