html {
    font-size: 1.30208333vw;
}

html body {
    font-size: 1.4rem;
    /** Support for scroll in IOS **/
    -webkit-overflow-scrolling: touch !important;
}

.container {
    width: 100vw;
    height: 100vh;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    padding: 0;
    margin: 0;
}

.content {
    -ms-flex: 1 1;
        flex: 1 1;
    overflow-y: scroll;
}

.menu {
    background: #ffffff;
    -ms-flex: 1 1;
        flex: 1 1;
    height: 100vh;
}

a:focus, a:hover {
    text-decoration: none;
}

[class*="col-"] {
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    min-height: 0.1rem;
}

.d_flex_center {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

/* form */
.form-control-feedback {
    width: auto;
    height: auto;
    line-height: 3.4rem;
}

.form-horizontal .form-group {
    margin-right: -1.5rem;
    margin-left: -1.5rem;
}

.form-group {
    margin-bottom: 1.5rem;
}

label {
    margin-bottom: 0.5rem;
}

.row {
    margin-right: -1.5rem;
    margin-left: -1.5rem;
}

.form-control {
    border-radius: 0;
}

.cancel button img,
.cancel-ic {
    width: 3rem;
    height: 3rem;
}

.modal-custom {
    width: 90%;
    top: 9rem;
    position: relative;
    background-color: #fff;
    padding: 2rem 0 0 0;
    height: 95rem;
    border-radius: 0.8rem;
    margin: auto;
}

.modal-page .search_header {
    height: 8rem;
    padding-top: 3.2rem;
    display: -ms-flexbox;
    display: flex;
}

.search_header .cancel img {
    width: 3rem;
    height: 3rem;
}

.rbc-date-cell .rbc-now,
.rbc-date-cell.rbc-now {
    font-weight: bold;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}

.recommended .slider-frame{
    padding: 1px 0!important;
    min-height: 25.4rem;
}

.div-modal {
    display: block;
    width: 100vm;
    height: 100vh;
}

.form-control-feedback,
.form-control-feedback img {
    pointer-events: auto!important;
}

img.history-back {
    width: 2rem!important;
    height: 3.4rem!important;
}

/* css file */
.disable-link {
    pointer-events: none;
}

/* user */
.user-ic,
.user-info-ic {
    width: 7rem;
    height: 7rem;
    border-radius: 50%;
}

.circle{
    border-radius: 50%!important;
}

a.user-info {
    display: -ms-flexbox;
    display: flex;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
}

.search_schedule a.user-info,
.search_calendar a.user-info {
    margin-left: 1rem;
}

.info-area {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    height: 8rem;
    -ms-flex-align: center;
        align-items: center;
}

.info {
    margin-left: 2rem;
}

.info-area div:last-child {
    font-size: 2.2rem;
    color: #bbbbbb;
}

.info-area div:first-child {
    font-size: 2.6rem;
    color: #444444;
}

.info-area div:last-child {
    font-size: 2.2rem;
    color: #bbbbbb;
}

.calendar_content .name,
.schedule_content .name {
    position: relative;
}

.calendar_content .name .private,
.schedule_content .name .private {
    position: absolute;
    right: 0.5rem;
    bottom: 5rem;
}

.calendar_content .name .private, .schedule_content .name .private {
    bottom: 2rem;
}

/* Image upload */
span.form-control{
    font-size: 2.7rem!important;
    display: -ms-inline-flexbox;
    display: inline-flex;
    padding: 3rem 2rem;
}

.img_item .fileContainer{
    webkit-box-shadow: none;
    -webkit-box-shadow:none!important;
            box-shadow:none!important;
    border-radius:0!important;
    padding: 0!important;
    margin: 0!important;
    background: none!important;
}

.fileUploader .fileContainer .chooseFileButton{
    padding: 0;
    background: none;
    border-radius: 0;
    color: #CCC;
    font-weight: 300;
    font-size: inherit;
    margin: 0;
    -webkit-transition: all 0.2s ease-in;
    -o-transition: all 0.2s ease-in;
    transition: all 0.2s ease-in;
    cursor: pointer;
    outline: none;
    border: none;
}

.fileUploader .fileContainer .chooseFileButton:hover{
    background: none;
}

